var recommendJson = require("../json/recommend.json");

var CartContainer = React.createClass({
    getInitialState:function(){
        return {
            recommendList:recommendJson.RecommendItems,
            hidden:"show",//
            isRem:"show",//
            modify:"",//  
            isCart:"hidden",// 
            isActive:true,// 
            isThis:true,// li
            isOperate:true//
        }
    },
    componentWillMount:function(){
        if(localStorage.getItem("user")){
            this.setState({
                hidden:"hidden",
            })
        }
        if(localStorage.getItem("cartNum")){
            this.setState({
                modify:"",
                isCart:"show",
                isRem:"hidden"
            })
        }
    },
    clickFun:function(){// all
        this.setState({
            isActive:!this.state.isActive
        })
    },
    clickThis:function(e){// li 
        // console.log(this.state.isThis);
        if(this.state.isThis){
            $(e.target).removeClass("active");
            this.state.isThis = false;
        }else{
            $(e.target).addClass("active");
            this.state.isThis = true;
        }
        
    },
    modifyFun:function(){

        if(this.state.isOperate){
            $(".cartDisplay").addClass("hidden");
            $(".cartOperate").removeClass("hidden");
            $(".total_3").addClass("hidden");
            $(".total_4").removeClass("hidden");
            // this.state.isOperate = false;
            this.setState({
                modify:"",
                isOperate : false
            })
        }else{
            $(".cartDisplay").removeClass("hidden");
            $(".cartOperate").addClass("hidden");
            $(".total_3").removeClass("hidden");
            $(".total_4").addClass("hidden");
            // $(".cartDisplay").removeClass("hidden");
            // $(".cartOperate").removeClass("show");
            // $(".cartDisplay").addClass("show");
            // $(".cartOperate").addClass("hidden");
            // this.state.isOperate = true;
            this.setState({
                modify:"",
                isOperate : true
            })
        }
    },
    render:function(){
        var list = this.state.recommendList;
        // console.log(list);
        var domList = list.map(function(item,index){
            return <RecommendItem key={index} src={"http://i.lifevccdn.com"+item.ImageUrl} text={item.Name} price={item.SalePrice}/>
        })
        var width = 1.8*domList.length;
        width = width+"rem";

        var cartList = [];
        if(localStorage.getItem("user")){
            for(var i = 1;i<localStorage.length-1;i++){
                var keytxt = localStorage.key(i);
                var cartObj = JSON.parse(localStorage.getItem(keytxt));
                cartList.push(cartObj);
            }
        }else{
            for(var i = 1;i<localStorage.length;i++){
                var keytxt = localStorage.key(i);
                var cartObj = JSON.parse(localStorage.getItem(keytxt));
                cartList.push(cartObj);
            }
        }

        var active = "";
        if(this.state.isActive){
            active = "active"
        }
        // console.log(cartList);
        var that = this;
        var cartCount = cartList.length;//
        var priceList = [];
        var sum = 0;//
        var cartDom = cartList.map(function(item,index){
            priceList.push(item.goodsPrice*item.goodsNum);
            return <CartItem key={index} goodsSrc={item.goodsSrc} goodsName={item.goodsName} goodsPrice={item.goodsPrice} 
                                goodsNum={item.goodsNum} goodsTag={item.goodsTag} clickThis={that.clickThis} active={active}
                                />
        })
        for(var i =0;i<priceList.length;i++){
            sum += priceList[i];
        }
       
        return (
            <div className="cartContainer">
                <span className="modify" onClick={this.modifyFun}>{this.state.modify}</span>
                <div className={"login "+this.state.hidden}>
                    <a href="#login"><button></button></a><span></span>
                </div>
                <div className="exchange">
                    <input placeholder=""/><button></button>
                </div>
                <div className="cartMain">
                    <div className={"cartEmpty "+this.state.isRem}>
                        <h6></h6>
                        <p></p>
                    </div>
                    <div className={"cartList "+this.state.isCart}>
                        <ul>
                            {cartDom}
                        </ul>
                        <div className="cartAddress"><button>&nbsp;&nbsp;&nbsp;v</button><span>69</span></div>
                        <div className="cartTotal">
                            <div className="total_1"><span className={"select iconfont icon-duigou "+active} onClick={this.clickFun}></span></div>
                            <div className="total_2">
                                <p className="tp1"><span className="red"> {sum}</span></p>
                                <p className="tp2">  {sum} -   0</p>
                            </div>
                            <a className="total_3">{cartCount}</a>
                            <div className="hidden total_4"></div>
                        </div>
                    </div>
                </div>
                <div className={"recommend "+this.state.isRem}>
                    <p>Rico.S</p>
                    <i></i>
                    <div>
                        <ul style={{width}}>
                            {domList}
                        </ul>
                    </div>
                </div>
                <div></div>
            </div>
        )
    }
})
var RecommendItem = React.createClass({
    render:function(){
        return (
            <li>
                <a href="#">
                    <img src={this.props.src}/>
                    <h5>{this.props.text}</h5>
                    <p className="price">{this.props.price}</p>
                </a>
            </li>
        )
    }
})

var CartItem = React.createClass({
    render:function(){
        return (
            <li>
                <span className={"select iconfont icon-duigou "+this.props.active} onClick={this.props.clickThis}></span>
                <a>
                    <img src={this.props.goodsSrc}/>
                </a>
                <div className="cartDisplay">
                    <h3><i>{this.props.goodsName}</i><span> {this.props.goodsPrice}</span></h3>
                    <h6>{this.props.goodsTag}</h6>
                    <p>X {this.props.goodsNum}</p>
                </div>
                <div className="cartOperate hidden">
                    <p><span>{this.props.goodsTag}<e className="iconfont icon-xiangxia"></e></span><i className="iconfont icon-shanchu"></i></p>
                    <div><span>-</span><span>{this.props.goodsNum}</span><span>+</span></div>
                </div>
            </li>
        )
    }
})

module.exports = CartContainer;


