var Register = React.createClass({
    getUpdate:function(){
        $("#update-img").attr("src","http://account.lifevc.com/Account/NewVerifyCode?t=1499310484644?rnd=" + Math.random());
    },
    regBtnFun:function(){
        var user = $("#regNum").val();
        var pwd = $("#regPwd").val();
        var pho = $("#regPho").val();
        var mobie = $("#regMobie").val();
        //console.log(user);
      
        if(!(/^1[34578]\d{9}$/.test(user))){ 
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("");
        }else if(!(/^[A-Za-z0-9]{6,20}$/).test(pwd)){
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("6-20");
        }else if(pho == ""){
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("");
        }else if(mobie == ""){
            $(".alertDiv").show().delay(3000).fadeOut();
            $(".alertDiv").html("");
        }else{
            $.ajax({
				url:"http://localhost:8080/register",
				type:"post",
				data:{
					user:user,
					pwd:pwd
				},
				success:function(data){
					console.log(data)				
				}
			})
			localStorage.setItem("user",user) ;
			window.location.href="#/usercenter";	
        }
    },
    render:function(){
        return (
            <div className="register-main">
                <h1></h1>
                <ul>
                    <li><input type="tel" id="regNum" className="ipt" placeholder="" required="required" maxLength="11"/></li>
                    <li><input type="text" id="regPwd" className="ipt" placeholder="6-20," required="required" maxLength="20"/></li>
                    <li><input type="text" id="regPho" className="ipt" placeholder="" required="required" maxLength="20"/>
                        <div className="verify">
                            <img src="http://account.lifevc.com/Account/NewVerifyCode?t=1499310484644" id="update-img"/>
                            <img src="img/update.png" id="update" onClick={this.getUpdate}/>
                        </div>
                    </li>
                    <li>
                        <input type="text" id="regMobie" className="ipt" placeholder="" required="required" maxLength="20"/>
                        <a></a>
                    </li>
                </ul>
                <p><a href="javascript:;"></a></p>
                <div className="btn-register"><a id="regBtn" onClick={this.regBtnFun}></a></div>
                <div className="btn-login"><a href="#login"></a></div>
                <div className="alertDiv"></div>
            </div>
        )
    }
})

module.exports = Register;

